<template>
  <div class="info-div " style="margin-top:1em;height:calc(100% - 1.5em)">
    <div class="info-header">
      <span class="callback" @click="resetComponent">重点用户</span>
      <span> > 用能详情</span>
    </div>
    <div class=" list-content detail-content infoCon list-contentB" style="padding:0.8em 1.2em!important">
      <ul class="form-list commInfoCon">
        <li>
          <span class="label-show">用户姓名：&nbsp;</span>
          <span class="label-show text-show">{{row.customerName}}</span>
        </li>
        <li>
          <span class="label-show">表号：&nbsp;</span>
          <span class="label-show text-show">{{row.deviceNo}}</span>
        </li>
        <li>
          <span class="label-show">表计户号&nbsp;</span>
          <span class="label-show text-show">{{row.meterUserNo || '--'}}</span>
        </li>

      </ul>
      <timer style="float:right;margin-right: 0em;" :startDate="startDate" :endDate="endDate" :deadLine="deadLine"
             @getTimer="getTime"></timer>
      <div style="float: right;margin-right:1em">

