        <selects :option="latitude" @getSelect="getOption"></selects>
      </div>
      <div class="detailCon">
        <div class="detailInfo" style="height:40%;margin-top:0" ref="lineContent">
          <!-- <div class="h5-headers">   <span style="line-height:1">用能情况分析</span></div> -->
          <div id="topChart" class="topChart"></div>
        </div>
        <div class="detailInfo" style="height:60%;" ref="lineContent">
          <!-- <div class="h5-headers">   <span style="line-height:1">用能数据详情</span></div> -->
          <div ref="myOwnTable" class="table-container noBorderTable" style="height:calc(100%);">
            <tables v-if="showTable" :tableOpt="tableOption" ref="myTable" @rowDbClick="" @freshStaticTable="getTableData"></tables>
          </div>
        </div>
      </div>
    </div>
    <dialogs type="customer" width="50em" ref="dialogChart" v-if="showLine" overflow="auto">
      <div slot="header">
        {{lineTitle}}
      </div>
      <div slot="content" style="width:100%;">
        <div id="dialogChart" style="width:100%;height: 19em"></div>
      </div>
    </dialogs>
  </div>
</template>
<script>
  import timer from '../../component/childComponent/Timer.vue'
 // import echarts from 'echarts'

  export default {
    data() {
      return {
        showLine: false,
        showTable: false,
        lineTitle: "",
        lineChart: '',
        startDate: '',
        endDate: '',
        searchParam: {
          dataMark: '',
          model: 1,
          customerNo: '',
          meterUserNo: '',
          deviceType: '07'
        },
        latitude: {
          cs: 'client',
          width: '6em',
          data: [
            {text: '年', value: 1},
            {text: '月', value: 2},
            {text: '日', value: 3}
          ],
          constant: {text: '日', value: 3},
          key: 'searchParam.model'
        },
        chartXData: [],
        chartYData: [],
        tableOption: {
          rowName: 'data',
          column: [
            {title: '抄表时间', field: 'collectDate', textAlign: 'center', width: 130},
            // {title: '总', field: 'collectDate', textAlign: 'center', width: 60},
            // {title: '剩余金额', field: 'collectDate', textAlign: 'center', width: 100},
            // {title: '表计购电总金额', field: 'collectDate', textAlign: 'center', width: 160},
            // {title: '表计购电次数', field: 'collectDate', textAlign: 'center', width: 140},
            {title: '正向有功总电量', field: 'activeTotal', textAlign: 'center', width: 120},
            {title: '正向有功总示数', field: 'activeTotalValue', textAlign: 'center', width: 120},
            // {title: '正向无功总', field: 'unactiveTotal', textAlign: 'center', width: 120},
            {
              title: 'A相电流',
              field: 'currentA',
              textAlign: 'center',
              width: 120,
              type: 'object',
              formatter: function (val, index, row, obj) {
                return {
                  template: '<button class="list-operate" @click="btnClick">详情</button>',
                  methods: {
                    btnClick: function () {
                      obj.$parent.getLineDetail(0, row)
                    }
                  }
                }
              }
            },
            {
              title: 'B相电流',
              field: 'currentB',
              textAlign: 'center',
              type: 'object',
              width: 120,
              formatter: function (val, index, row, obj) {
                return {
                  template: '<button class="list-operate" @click="btnClick">详情</button>',
                  methods: {
                    btnClick: function () {
                      obj.$parent.getLineDetail(1, row)
                    }
                  }
                }
              }
            },
            {
              title: 'C相电流',
              field: 'currentC',
              textAlign: 'center',
              type: 'object',
              width: 120,
              formatter: function (val, index, row, obj) {
                return {
                  template: '<button class="list-operate" @click="btnClick">详情</button>',
                  methods: {
                    btnClick: function () {
                      obj.$parent.getLineDetail(2, row)
                    }
                  }
                }
              }
            },
            {
              title: 'A相电压',
              field: 'voltageA',
              textAlign: 'center',
              type: 'object',
              width: 120,
              formatter: function (val, index, row, obj) {
                return {
                  template: '<button class="list-operate" @click="btnClick">详情</button>',
                  methods: {
                    btnClick: function () {
                      obj.$parent.getLineDetail(3, row)
                    }
                  }
                }
              }
            },
            {
              title: 'B相电压',
              field: 'voltageB',
              textAlign: 'center',
              type: 'object',
              width: 120,
              formatter: function (val, index, row, obj) {
                return {
                  template: '<button class="list-operate" @click="btnClick">详情</button>',
                  methods: {
                    btnClick: function () {
                      obj.$parent.getLineDetail(4, row)
                    }
                  }
                }
              }
            },
            {
              title: 'C相电压',
              field: 'voltageC',
              textAlign: 'center',
              type: 'object',
              width: 120,
              formatter: function (val, index, row, obj) {
                return {
                  template: '<button class="list-operate" @click="btnClick">详情</button>',
                  methods: {
                    btnClick: function () {
                      obj.$parent.getLineDetail(5, row)
                    }
                  }
                }
              }
            }
          ],
          data: [],
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          queryParam: {},
          exportURL: 'importantCustomer/downLoadImpDetailByDay',
          noExportCurrent: true
        },
        allType: [
          {type: 'current_a', name: 'A相电流', unit: 'A'},
          {type: 'current_b', name: 'B相电流', unit: 'A'},
          {type: 'current_c', name: 'C相电流', unit: 'A'},
          {type: 'voltage_a', name: 'A相电压', unit: 'V'},
          {type: 'voltage_b', name: 'B相电压', unit: 'V'},
          {type: 'voltage_c', name: 'C相电压', unit: 'V'}
        ],
        currentType: 0,
        dialogChart: '',
        ABLineData: {
          'voltageA': [],
          'voltageB': [],
          'voltageC': [],
          'currentA': [],
          'currentB': [],
          'currentC': [],
          'xData': []
        }
      }
    },
    props: ['row', 'deviceTypes'],
    components: {
      timer
    },
    methods: {
      //获取单块表的ABC电流、电压曲线
      getLineDetail: function (type, row) {
        let that = this;
        that.currentType = type;
        let p = {};
        for(let item in that.searchParam) {
             p[item] = that.searchParam[item];
        }
       
        p.searchType = that.allType[type].type;
        p.dataMark = row.collectDate;
        p.model = that.searchParam.model == 1? 2: 3;
        let typeInfo = that.allType[type];
        that.lineTitle = that.allType[type].name + '详情 ( ' + row.collectDate + ' )';
        let option = {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            },
            formatter: function (param) {
              return typeInfo.name + '：' + param[0].value + ' ' + typeInfo.unit + '<br />采集时间：' + param[0].axisValue + '';
            }
          },
          axisPointer: {
            link: {xAxisIndex: 'all'}
          },
          grid: {
            left: 50,
            right: 50,
            bottom: 40,
            top: 30
          },
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              data: [],
              axisLabel: {
                textStyle: {
                  color: '#fff'
                }
              },
              axisLine: {
                onZero: true,
                lineStyle: {
                  color: '#576373'
                }
              }
            }
          ],
          yAxis: [
            {
              name: '',
              nameTextStyle: {
                color: '#fff',
              },
              type: 'value',
              axisLabel: {
                color: '#fff',
                formatter: '{value}',
                textStyle: {
                  color: '#fff'
                }
              },
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              splitLine: {
                lineStyle: {
                  color: '#576373'
                }
              }
            }
          ],
          series:
            {
              name: '',
              type: 'line',
              symbolSize: 8,
              hoverAnimation: false,
              smooth: true,
              data: [],
              itemStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#F96362'},
                      {offset: 0.5, color: '#CC5A5E'},
                      {offset: 1, color: '#974F5A'}
                    ]
                  )
                },
                emphasis: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#2378f7'},
                      {offset: 0.7, color: '#2378f7'},
                      {offset: 1, color: '#83bff6'}
                    ]
                  )
                }
              }
            }
        };
        p.dataMark = row.collectDate;
        that.getRequest({
          url: 'importantCustomer/queryImportantABC',
          method: 'post',
          param: p,
          success: function (data) {

            let Data = data.data;
            let len = Data.length;
            let xData = [], yData = [];

            for (let i = 0; i < len; i++) {
              let xval = Data[i].xAxis;
              xData.push(xval.substring(0, xval.length - 3));
              yData.push(Data[i].yAxis);
            }

            option.series.data = yData;
            option.xAxis[0].data = xData;
            that.showLine = true;
            setTimeout(function () {
              that.$refs.dialogChart.show({
                hasClose: true,
                action: {
                  cancel: function () {
                    that.$refs.dialogChart.close()
                  }
                }
              });
              that.$nextTick(function () {
                that.dialogChart = echarts.init(document.getElementById('dialogChart'));
                that.dialogChart.setOption(option, true);
              })
            }, 10);
          }
        })
      },
      getTime: function (val) {

        if (val != this.searchParam.dataMark) {
          if (this.searchParam.dataMark !== '') {
            this.searchParam.dataMark = val;
           // this.loadCharts();
            this.getTableData();
          } else {
            this.searchParam.dataMark = val;
          }
        }
      },
      resetComponent: function () {
        this.$emit('closeDiv');
      },
      //生成顶部柱状图（电量曲线）
      loadCharts: function () { 
        this.chartXData = [];
        this.chartYData = [];
    
          this.lineChart = echarts.init(document.getElementById('topChart'));
          let option = {

            tooltip: {
              formatter: function (param) {

                if (that.searchParam.model == 3){
                  let unit = (param.seriesIndex == 1 ||  param.seriesIndex == 2 ||  param.seriesIndex == 3) ? 'V' : (param.seriesIndex == 0 ? 'kwh' : 'A');
                  return param.seriesName +'： ' + param.value + unit +' <br />时间：' + that.searchParam.dataMark + ' ' + param.name + '';
                }
                else
                  return param.seriesName+'： ' +param.value + 'kwh <br />时间：' + param.name + '';
              }
            },
            legend: {
            //  x: 'right',
            right: '0',
            data: ['正向有功总电量'],
            itemGap: 20,
            textStyle: {
              color: "#ffffff",
            },
            selected: {
              '正向有功总电量': true
            },
            selectedMode: 'single'
          },
          grid: {
            left: 30,
            right: 30,
            bottom: 30,
            top: 30,
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: this.chartXData,
            axisLabel: {
              color: '#fff',
              textStyle: {
                color: '#fff'
              }
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: '#576373'
              }
            }
          },
          yAxis: {
            name: 'KWH/V/A',
            type: 'value',
            nameTextStyle: {
              color: '#fff',
              fontSize: 13
            },

            axisLabel: {
              color: '#fff',
              textStyle: {
                color: '#fff'
              }
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: '#576373'
              }
            }
          },
          series: [
          {
            name: '正向有功总电量',
            barMaxWidth: 100,
            type: 'bar',
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0, 0, 0, 1,
                  [
                  {offset: 0, color: 'rgba(249,99,98, 1)'},
                  {offset: 0.7, color: 'rgba(249,99,98, 0.6)'},
                  {offset: 1, color: 'rgba(249,99,98, 0.4)'},
                  ]
                  )
              }
            },
            data: []
          }
          ]
         };
        let that = this;
        that.getRequest({
          url: 'importantCustomer/queryImportantCurve',
          method: 'post',
          param: that.searchParam,
          success: function (data) {
            let Data = data.data;
            let len = Data.length;
            that.chartXData = [];
            that.chartYData = [];
            for (let i = 0; i < len; i++) {
              let item = Data[i];
              if (that.searchParam.model == 3) {
                let xval = item.xAxis.split(' ')[1];
                that.chartXData.push(xval.substring(0, xval.length - 3));
                that.chartYData.push(item.yAxis);
              } else {
                that.chartXData.push(item.xAxis);
                that.chartYData.push(item.yAxis);
              }
            }
            option.xAxis.data =   that.chartXData;
            option.series[0].data = that.chartYData;
            if (that.searchParam.model == 3) {
              option.series.push({ name: 'A压',type: 'line', data: that.ABLineData.voltageA});
              option.series.push({ name: 'B压',type: 'line', data: that.ABLineData.voltageB});
              option.series.push({ name: 'C压',type: 'line', data: that.ABLineData.voltageC});
              option.series.push({ name: 'A流',type: 'line', data: that.ABLineData.currentA});
              option.series.push({ name: 'B流',type: 'line', data: that.ABLineData.currentB});
              option.series.push({ name: 'C流',type: 'line', data: that.ABLineData.currentC});
              option.legend.data = ['正向有功总电量', 'A压', 'B压', 'C压', 'A流', 'B流', 'C流'];
              option.legend.selected = {
                '正向有功总电量': true,
                'A压': false,
                'B压': false,
                'C压': false,
                'A流': false,
                'B流': false,
                'C流': false
              }
            }
            that.$nextTick(function () {
              that.lineChart.setOption(option, true);
              that.lineChart.on('legendselectchanged', function (params) {
                 option.legend.selected = params.selected;
                 let isSelected = params.name;
                 if(isSelected == '正向有功总电量') {
                    option.xAxis.data =   that.chartXData
                 } else {
                    option.xAxis.data =   that.ABLineData.xData;
                 }
                 that.lineChart.setOption(option, true);
              });
            });
          }
        })
      },
      //查询底部的table表格
      getTableData: function () {
        let that = this;
        that.ABLineData = {
          'voltageA': [],
          'voltageB': [],
          'voltageC': [],
          'currentA': [],
          'currentB': [],
          'currentC': [],
          'xData': []
        };
        //按年月汇总时，表格显示汇总信息
        let columnYM = [
          {title: '抄表时间', field: 'collectDate', textAlign: 'center', width: 130},
          {title: '正向有功总电量', field: 'activeTotal', textAlign: 'center', width: 120},
          {title: '正向有功总示数', field: 'activeTotalValue', textAlign: 'center', width: 120},
          {
            title: 'A相电流',
            field: 'currentA',
            textAlign: 'center',
            width: 120,
            type: 'object',
            formatter: function (val, index, row, obj) {
              return {
                template: '<button class="list-operate" @click="btnClick">详情</button>',
                methods: {
                  btnClick: function () {
                    obj.$parent.getLineDetail(0, row)
                  }
                }
              }
            }
          },
          {
            title: 'B相电流',
            field: 'currentB',
            textAlign: 'center',
            type: 'object',
            width: 120,
            formatter: function (val, index, row, obj) {
              return {
                template: '<button class="list-operate" @click="btnClick">详情</button>',
                methods: {
                  btnClick: function () {
                    obj.$parent.getLineDetail(1, row)
                  }
                }
              }
            }
          },
          {
            title: 'C相电流',
            field: 'currentC',
            textAlign: 'center',
            type: 'object',
            width: 120,
            formatter: function (val, index, row, obj) {
              return {
                template: '<button class="list-operate" @click="btnClick">详情</button>',
                methods: {
                  btnClick: function () {
                    obj.$parent.getLineDetail(2, row)
                  }
                }
              }
            }
          },
          {
            title: 'A相电压',
            field: 'voltageA',
            textAlign: 'center',
            type: 'object',
            width: 120,
            formatter: function (val, index, row, obj) {
              return {
                template: '<button class="list-operate" @click="btnClick">详情</button>',
                methods: {
                  btnClick: function () {
                    obj.$parent.getLineDetail(3, row)
                  }
                }
              }
            }
          },
          {
            title: 'B相电压',
            field: 'voltageB',
            textAlign: 'center',
            type: 'object',
            width: 120,
            formatter: function (val, index, row, obj) {
              return {
                template: '<button class="list-operate" @click="btnClick">详情</button>',
                methods: {
                  btnClick: function () {
                    obj.$parent.getLineDetail(4, row)
                  }
                }
              }
            }
          },
          {
            title: 'C相电压',
            field: 'voltageC',
            textAlign: 'center',
            type: 'object',
            width: 120,
            formatter: function (val, index, row, obj) {
              return {
                template: '<button class="list-operate" @click="btnClick">详情</button>',
                methods: {
                  btnClick: function () {
                    obj.$parent.getLineDetail(5, row)
                  }
                }
              }
            }
          }];
        //按照日汇总时，显示重点用户数据
        let columnD = [
          {title: '抄表时间', field: 'collectDate', textAlign: 'center', width: 130},
          {title: '正向有功总电量', field: 'activeTotal', textAlign: 'center', width: 120},
          {title: '正向有功总示数', field: 'activeTotalValue', textAlign: 'center', width: 120},
          {title: 'A相电流', field: 'currentA', textAlign: 'center', width: 120},
          {title: 'B相电流', field: 'currentB', textAlign: 'center', width: 120},
          {title: 'C相电流', field: 'currentC', textAlign: 'center', width: 120},
          {title: 'A相电压', field: 'voltageA', textAlign: 'center', width: 120},
          {title: 'B相电压', field: 'voltageB', textAlign: 'center', width: 120},
          {title: 'C相电压', field: 'voltageC', textAlign: 'center', width: 120}];
        //that.searchParam.model==3 标识按日显示表格
        let column = that.searchParam.model == 3 ? columnD : columnYM;
        let url = that.searchParam.model == 3 ? 'importantCustomer/queryImpDetailByDay' : 'importantCustomer/queryImpDetail';
        that.getRequest({
          url: url,
          method: 'post',
          param: that.searchParam,
          success: function (data) {
            let Data = data.data;
            if (that.searchParam.model == 3) {
              that.ABLineData.currentA = [];
              that.ABLineData.currentB = [];
              that.ABLineData.currentC = [];
              that.ABLineData.voltageA = [];
              that.ABLineData.voltageB = [];
              that.ABLineData.voltageC = [];
              that.ABLineData.xData = [];
              for(let i=Data.length - 1; i>-1; i--) {
                 that.ABLineData.currentA.push(Data[i].currentA);
                 that.ABLineData.currentB.push(Data[i].currentB);
                 that.ABLineData.currentC.push(Data[i].currentC);
                 that.ABLineData.voltageA.push(Data[i].voltageA);
                 that.ABLineData.voltageB.push(Data[i].voltageB);
                 that.ABLineData.voltageC.push(Data[i].voltageC);
                 that.ABLineData.xData.push(Data[i].collectDate.substring(11, Data[i].collectDate.length - 3))
               }  
            }
            
            if (that.showTable) {
              that.$refs.myTable.resetTableOpt({
                data: Data,
                column: column,
                isExport: that.searchParam.model == 3,
                queryParam: that.searchParam
              })
            } else {
              that.tableOption.data = Data;
              that.tableOption.isExport = that.searchParam.model == 3;
              that.showTable = true;
               that.tableOption.queryParam = that.searchParam
            }
            that.loadCharts();
          }
        })
      }
    },
    computed: {
      deadLine() {
        let date = new Date();
        this.endDate = date.getFullYear();
        this.startDate = date.getFullYear() - 1;
        switch (this.searchParam.model) {
          case 1: {
            return 'year';
          }
          case 2: {
            return 'month';
          }
          case 3:
            return 'day';
        }
      }
    },
    created: function () {
      let date = new Date();
      this.endDate = date.getFullYear();
      this.startDate = date.getFullYear() - 1;
      this.searchParam.customerNo = this.row.customerNo;
      this.searchParam.meterUserNo = this.row.meterUserNo;
    },
    mounted: function () {
      
      this.getTableData();
    },
    watch: {
      'searchParam.model'(val) {

      }
    }
  }
</script>
<style lang="scss">
  .infoCon {
    height: calc(100% - 1em) !important;
    
    margin-top: 0 !important;
    .label-show {
      font-size: 0.875em;
      margin-left: 0;
      min-width: 3em
    }
    .commInfoCon {
      padding-left: 0em;
      width: calc(100% - 40em);
      float: left;
      li {
        margin: 0;
        width: calc(100% / 3);
        .label-show + .text-show {
          text-align: left;
          max-width: calc(100% - 7em);
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }

    }
  }

  .detailCon {
    height: calc(100% - 3em);
    float: left;
    width: 100%;
    margin-top: 0.5em;
    .detailInfo {
      margin: 0;
      width: 100%;
      .h5-headers {
        padding: 0.5em 1em;
        text-align: left;
        font-size: 0.93em;
        margin-bottom: 0;
      }
    }
  }

  .topChart {
    width: 100%;
    height: calc(100%);
  }

  .bottomTable {
    width: 100%;
    height: calc(100% - 3em);
    margin-top: 1em
  }
</style>



