<template>
  <div :style="{height: showInfo ? 'calc(100% - 1em)' :'calc(100% - 3em)'}" >
    <div v-if="showInfo" class="info-container" >
        <info :row="currentRow" @closeDiv="closeDiv" ref="infoDetail" :deviceTypes="currentRow.deviceType"></info>
    </div>
  <div class="info-container" style="height:calc(100%);margin-top:3.2em">
    <div v-show="!showInfo" class="info-div" >
      <div class="info-header info-header-operate" style="right:0;">
      <!--  <selects ref="selects"  :option="selectOpt"   @getSelect="getOption"></selects> -->
       <template v-for="(item, $index) in formList">       
         <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" style="float:right"
                  @getInput="getFrom"></inputs>
                  
       </template>
      </div>
      <div class="list-content detail-content" style="height:calc(100% - 1.2em); padding: 0.5em 1em!important;">
       
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 1em)', padding:'0.5em 0', width: '100%', 'font-size': '0.94em'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="clickTableRow"></tables>
          </div>
    
      </div>
    </div>
    </div>
  <dialogs type="alert" ref="alerts"></dialogs>
  </div> 
</template>

<script>
   import info from './SpecialCustomerInfo.vue'
   export default {
     data() {
       return {
          showInfo: false,
          searchParam: {
            customerName: '',
            propertyName: '',
            customerContact: '',
            customerAddr: '',
            customerId: '',
            deviceNo: '',
            deviceType:'07'
          },
          tableHeight: '',
          tableShow: false,
          TableOpt: {
              column: [
                {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '160'},
                {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: '160'},
                {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '160'},
                {title: '表计类型', field: 'deviceType', textAlign: 'center', width: '160', type: 'string', 
                  formatter: function (val, index, item, obj) {
                    return obj.$parent.getEnumItem('allDeviceType', val).text;
                  }
                },
                {title: '表号', field: 'deviceNo', textAlign: 'center', width: '180'},
                 {title: '安装地址', field: 'installAddr', textAlign: 'center', width: '200'},
                // {title: '计费类型', field: 'dictItemName', textAlign: 'center', width: '180'},
                {title: '联系电话', field: 'customerContact', textAlign: 'center', width:'140'},
               
                {title: '部门机构', field: 'orgName', textAlign: 'center', width: '150'},
              ],
           
              url: 'importantCustomer/queryImportant',
              method: 'post',
              pageSize: 20,
              pageNumber: 1,
              pageList: [20, 40, 60]
            },
          currentRow: ''
       }
     },
     components: {
       info
     },
     computed: {
     	formList: function () {
        let that=this;
     	  return [ 
         {
            type: 'input',
            options: {
              type: 'typeSearch',
              key: 'searchParam',
              value: '',
              selectOpt: {
                width: '7em',
                cs: 'client',
                data: [
                  {text: '用户姓名', value: 'customerName'},
                  {text: '门牌编号', value: 'propertyName'},
                  {text: '联系电话', value: 'customerContact'},
                  {text: '用户地址', value: 'customerAddr'},
                  {text: '表计户号', value: 'meterUserNo'},
                  {text: '用户表号', value: 'deviceNo'}
                ],
                text: 'text',
                value: 'value',
                key: 'searchType',
                constant: {value: 'customerName'}
              }
            }
          }]
        }
     	},
     mounted: function () {
      let date = this.initDate();
      this.tableShow = true;
      let that = this;
     /* window.onresize = function () {
        that.windowChange()
      }*/
    },
     methods: {
       resizeCon: function () {

          let that = this;
            if (that.$refs.myTable) {
              that.$refs.myTable.onResize();
            }
            if (that.$refs.infoDetail) {

              let info = that.$refs.infoDetail;
            
              info.lineChart !== ''?  info.lineChart.resize() : '';
              if (info.$refs.myTable){
                info.$refs.myTable.resizeTable(info.$refs.myOwnTable.width, info.$refs.myOwnTable.clientHeight - 80);
              }
            }
       },
       windowChange: function () {
           let that = this;
           this.resizeCon();
            /*window.onresize = function () {
               that.resizeCon();
            }*/
          },
      closeDiv: function () {
        this.showInfo = false;
        this.$refs.myTable.onResize();
      },
       getFrom: function (val) {
         let key=val.key.split('.')[1];
         //let devType=this.searchParam["deviceType"];

         if (this.searchParam[key] !== val.val) {
            this.searchParam = this.cleanSearchData(this.searchParam);
            this.getOption(val, true);
            if(key!='deviceType')
            {
               // this.searchParam["deviceType"]=devType;
            }

            this.getSearchTable();
          }
       },
       getSearchTable: function () {
           let arr = this.getSearchData(this.searchParam);
           this.searchTable(arr, 1);
        },
       clickTableRow: function (row) {
         this.currentRow = row.val;
         this.showInfo = true;
       } 
      },
     
      watch: {}
   }
</script>

<style lang="scss">
	.specailTimer{
		float:right!important;
		.date-button {
          line-height: 1.7
		}
	}
</style>


