<template>
  <div>
     <selects ref="theYear" :option="yearOption" @getSelect="getSelectY" class="selectTimer"></selects>
   <!--   <label class="time_label">年</label>  -->
  	 <template v-if="deadLine != 'year'">  	 	  
  	 	  <selects ref="theMonth" :option="monthOption" @getSelect="getSelectM" class="selectTimer"></selects>
       <!--  <label  class="time_label">月</label>  -->
  	 </template>   
     <template v-if="deadLine == 'day'">     
     	 <selects  ref="theDay" :option="dayOption" @getSelect="getSelectD" class="selectTimer"></selects>
       <!--   <label  class="time_label">日</label> -->
     </template>
     
  </div>
</template>
<script>
  export default {
  	data() {
  		return{
          selectDate: {
          	year: '',
          	month: '',
          	day: ''
          },
          currentDate: '',
          startTime: {
          	year:'',
          	month: '',
          	day: ''
          },
          endTime: {
          	year:'',
          	month: '',
          	day: ''
          }
  		}
  	},
  	props: {
  		deadLine: { 
  			default: 'day'
  		},
  		startDate: {
  			
  			default: ''
  		},
  		endDate: {
  		
  			default: ''
  		}
  	},
  	computed: {
      //获取年数据
  		yearOption() {

  		  let start = this.selectDate.year - 20;	
  		  let end = this.selectDate.year + 20;
  		  if(this.startTime.year !== '') {
  		  	start = this.startTime.year ;
  		  }	
  		  if(this.endTime.year !== '') {
  		  	end = this.endTime.year;
  		  }
  		  let arr = [];
  	      for (let s = start; s<=end; s++) {
            arr.push({value: s, text: s+'年'});
  	      } 
  		  return {
             cs: 'client',
             data: arr,
             noWatch: true,
             width: '6.7em',
             key: 'selectDate.year',
             constant: {value: this.selectDate.year}
  		  }
  		},
      //获取月数据
  		monthOption() {
    
  		  return{
             cs: 'client',
             data: this.getAllMonth(),
             noWatch: true,
             width: '6.7em',
             key: 'selectDate.year',
             constant: {value: this.selectDate.month}
  		  }
  		},
      //获取日数据
  		dayOption() {
  		  return{
             cs: 'client',
             data: this.getAllDay(),
             noWatch: true,
             width: '6.7em',
             key: 'selectDate.day',
             constant: {value: this.selectDate.day}
  		  }
  		}
  	},
  	methods: {
      //获取日数据
  	   getAllDay() {
        let start = 1;	
  		  let end = this.getDaysInOneMonth(this.selectDate.year,this.selectDate.month);
  		  if(this.startTime.month !== '') {
  		  	if(this.startTime.year == this.selectDate.year && this.startTime.month == this.selectDate.month) {
               start = this.startTime.month;
  		  	}
  		  }	
  		  if(this.endTime.month !== '') {
  		  	if(this.endTime.year == this.selectDate.year && this.endTime.month == this.selectDate.month) {
               start = this.endTime.month;
  		  	}
  		  }
  		  let arr = [];
  	      for (let s = start; s<=end; s++) {
            arr.push({value: s, text: s+'日'});
  	      } 
  	      return arr
  	   },
       //获取月数据
  	   getAllMonth() {
          let start = 1;	
  		  let end = 12;
  		  if(this.startTime.month !== '') {
  		  	if(this.startTime.year == this.selectDate.year) {
               start = this.startTime.month;
  		  	}
  		  }	
  		  if(this.endTime.month !== '') {
  		  	if(this.endTime.year == this.selectDate.year) {
               start = this.endTime.month;
  		  	}
  		  }

  		  let arr = [];
  	      for (let s = start; s<=end; s++) {
            arr.push({value: s, text: s+'月'});
  	      } 
  	      return arr
  	   },	
       getDaysInOneMonth(year, month) {  
       	  
           return  new Date(Date.UTC(year, month, 0)).getUTCDate()
       },
       getSelectD(val) {
          val.val = parseInt(val.val);
          this.getOption(val, this);
          this.getAllDate();
       },
       resetDay(data) {
           if(this.deadLine == 'day') {
                this.$refs.theDay.resetOption({
                   data: this.getAllDay(),
                   constant: data ? data : ''
                 });
              }
       },
       getSelectM(val) {
          val.val = parseInt(val.val);
          if(val.val !== this.selectDate.month) {
            this.selectDate.month = val.val;
            this.resetDay();
            this.getAllDate();
          }
          
       },
       getSelectY(val) {
          val.val = parseInt(val.val);
          if(val.val !== this.selectDate.year) {
          	  this.selectDate.year = val.val;
              this.resetMonth();  
              this.getAllDate();
          }
       },
       resetMonth(data) {
          if(this.deadLine != 'year') {
              this.$refs.theMonth.resetOption({
                 data: this.getAllMonth(),
                 constant: data ? data : ''
              });
           }
       },
       getAllDate: function () {
         let day;

         switch(this.deadLine) {
         	case 'year':
         	  day = this.selectDate.year;
         	  break;
         	case 'month':
         	  day = this.selectDate.year + '-' + (this.selectDate.month < 10 ? ('0' + this.selectDate.month) : this.selectDate.month) ;
         	  break;
         	case 'day':
         	  day = this.selectDate.year + '-' + (this.selectDate.month < 10 ? ('0' + this.selectDate.month) : this.selectDate.month) + '-' + (this.selectDate.day < 10 ? ('0' + this.selectDate.day) : this.selectDate.day);
         	  break;
         }
       	 this.$emit('getTimer', day)
       },
       defaultDate() {
       	this.currentDate = new Date();
  	    this.selectDate.year =  this.currentDate.getFullYear();
  	    this.selectDate.month = this.deadLine != 'year' ? this.currentDate.getMonth() + 1 : '';
  	    this.selectDate.day = this.deadLine == 'day' ? this.currentDate.getDate() : '';
  	    if(this.startDate !== '') {
  	    let arrS = this.startDate.toString().split('-');
  		  this.startTime['year'] =  parseInt(arrS[0]);
  		  this.startTime['month'] =  parseInt(arrS[1] || 1);
  		  this.startTime['day'] =  parseInt(arrS[2] || 1);
  	    }	
  	    if(this.endDate !== '') {
  		  let arrE = this.endDate.toString().split('-');
  		  this.endTime['year'] =  parseInt(arrE[0]);
  		  this.endTime['month'] =  parseInt(arrE[1] || 1);
  		  this.endTime['day'] =  parseInt(arrE[2] || this.getDaysInOneMonth(this.endTime['year'], this.endTime['month']));
  	    }
       }
  	},
  	created: function () {
       this.defaultDate()
  	},
  	mounted: function () {
  	  
  	  this.getAllDate();
  	},
  	watch: { 
  	  deadLine(val) {
         switch (val){
         	case 'month': {
              let that = this;
              that.defaultDate();
         	  that.$nextTick(function () {
         	  	let date = new Date();
         	  	 if (that.selectDate.year == date.getFullYear()) {
         	  	 	  that.resetMonth({value:  that.selectDate.month});
         	  	 } else {
         	  	 	that.resetMonth();
         	  	 } 
               
         	  });
         	  break;
         	 }
         	case 'day': {
         	  let that = this;
         	  that.defaultDate();
         	  that.$nextTick(function () {
         	  	 let date = new Date();
                that.resetMonth({value:  that.selectDate.month});
         	  	 if (that.selectDate.month == (date.getMonth() + 1) && that.selectDate.year == date.getFullYear()) {
         	  	 	  that.resetDay({value:  that.selectDate.day});
         	  	 } else {
         	  	 	that.resetDay();
         	  	 } 
                
         	  });
         	  break;
         	}
         }
         this.getAllDate();
  	  }
  	}
  }
</script>
<style lang="scss">
   .time_label{
   	display: block;
   	float:left;
   	line-height:2.5;
   	margin: 0 0.5em
   }
   .selectTimer{
      border-radius: 0;
       
   }
</style>


